@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensans.woff') format('woff');
  src: url('../fonts/opensans.woff2') format('woff2');
  font-weight: normal;
	font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensansbold.woff') format('woff');
  src: url('../fonts/opensansbold.woff2') format('woff2');
  font-weight: bold;
	font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensanslight.woff') format('woff');
  src: url('../fonts/opensanslight.woff2') format('woff2');
  font-weight: 300;
	font-style: normal;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans';
  font-size: 14px;
  line-height: 24px;
  color: $main-color;
  background-color: #ffffff;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.devider {
  background-image: url('/img/bg-triangle-white.svg');
  background-repeat: no-repeat;
  background-position: top;
  background-size: contain;

  background-color: $bg-color;

  width: 100%;
  height: 32px;
}

.devider-footer {
  background-image: url('/img/triangle.png');
  background-repeat: no-repeat;
  background-position: bottom;

  position: absolute;
  z-index: 1;

  width: 100%;
  height: 33px;
}

.page {
  width: $mobile-width;
  margin: 0 auto;
}

.page-main {
  padding-top: 260px;
}

.pageform {
  width: $mobile-width;
  margin: 0 auto;
}

.pagephoto {
  width: $mobile-width;
  margin: 0 auto;
}

.button {
  border: none;
  background-color: inherit;
  cursor: pointer;

  padding: 0;
}

.container {
  padding: 0 20px;
}

@media (min-width: $tablet-width) {
  .page {
    width: $tablet-width;
  }

  .pageform {
    width: $tablet-width;
  }

  .pagephoto {
    width: $tablet-width;
  }

  .page-main {
    padding-top: 276px;
  }

  .devider {
    height: 54px;
  }

  .devider-footer {
    height: 70px;
  }
}

@media (min-width: $desktop-width) {
  .page {
    min-width: 1200px;
  }

  .pageform {
    min-width: 1200px;
  }

  .pagephoto {
    min-width: 1200px;
  }

  .page-main {
    padding-top: 0;
  }

  .devider {
    height: 94px;
  }

  .devider-footer {
    height: 122px;
  }
}
